<template>
    <div>
        <in-body :mbx="['首页','布局','Grid']">
            <Row>
                <Col span="12">col-12</Col>
                <Col span="12">col-12</Col>
            </Row>
            <br>
            <Row>
                <Col span="8">col-8</Col>
                <Col span="8">col-8</Col>
                <Col span="8">col-8</Col>
            </Row>
            <br>
            <Row>
                <Col span="6">col-6</Col>
                <Col span="6">col-6</Col>
                <Col span="6">col-6</Col>
                <Col span="6">col-6</Col>
            </Row>
        </in-body>
    </div>

</template>

<script>
    import inBody from '../inBody'
    export default {
        name: "myGrid"
        ,components:{
            inBody
        }
    }
</script>

<style scoped>
    .ivu-col{
        color: #fff;
        padding: 10px 0;
        text-align: center;
        background-color: rgba(0,153,229,.9);
        height: 40px;
    }
    .ivu-col:nth-child(odd) {
        background: rgba(0,153,229,.7);

    }
</style>


